@charset "UTF-8";
.post-content h1, .post-content h2, .post-content h3, .post-content h4, .post-content h5, .post-content h6 {
  position: relative;
  line-height: 26px;
  margin: 18px 0;
  font-size: 18px;
}
.post-content h1:before, .post-content h2:before, .post-content h3:before, .post-content h4:before, .post-content h5:before, .post-content h6:before {
  content: "";
}
.post-content h1 {
  padding: 0 15px 0 15px;
  font-size: 22px;
}
.post-content h1:before {
  font-size: 22px;
  content: "¶";
  position: absolute;
  left: -5px;
  top: 0;
  color: #38bdf8;
}
.post-content h2 {
  padding: 0 15px;
  font-size: 20px;
}
.post-content h2:before {
  position: absolute;
  display: block;
  top: 10%;
  bottom: 10%;
  left: 0;
  width: 4px;
  border-radius: 2px;
  background-color: #38bdf8;
}
.post-content h3 {
  padding: 0 15px;
}
.post-content h3:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background-color: #38bdf8;
  border-radius: 0 4px 4px 0;
}
.post-content h4 {
  margin-left: -10px;
}
.post-content h4:before {
  content: "「";
  font-weight: 600;
  margin-right: 5px;
  color: #38bdf8;
}
.post-content h4:after {
  content: "」";
  font-weight: 600;
  margin-left: 5px;
  color: #38bdf8;
}
.post-content h5 {
  padding: 0 15px;
}
.post-content h5::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -8px;
  width: 20px;
  height: 20px;
  background-image: url("data:image/png;base64,");
  background-size: 100% 100%;
}
.post-content h6 {
  padding: 0 15px;
}
.post-content h6::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -8px;
  width: 20px;
  height: 20px;
  background-image: url("data:image/png;base64,");
  background-size: 100% 100%;
}
.post-content hr {
  border-bottom: 2px solid #0ea5e9;
  margin: 16px auto;
}
.post-content p {
  line-height: 24px;
  margin: 0 0 16px;
}
.post-content p:last-child {
  margin-bottom: 0;
}
.post-content blockquote {
  margin: 0 auto 0.5em;
  padding: 12px;
  border: 0;
  background-color: #eff6ff;
  color: #3b82f6;
  line-height: 26px;
  border-radius: 6px;
  position: relative;
  text-indent: 2em;
}
.post-content blockquote:last-child {
  margin-bottom: 0;
}
.post-content blockquote:before {
  content: "“";
  font-size: 4em;
  font-family: monospace;
  position: absolute;
  height: 2em;
  width: 2em;
  left: -0.8em;
  top: 0.4em;
}
.post-content blockquote:last-child {
  margin-bottom: 0;
}
.post-content blockquote p {
  margin: 0;
}
.post-content a:not([class]) {
  line-height: 26px;
  color: #2563eb;
  text-decoration: none;
  transition: all 0.146s;
}
.post-content a:not([class]):hover {
  color: #0284c7;
  text-decoration: underline;
}
.post-content ol {
  list-style: decimal;
  padding-left: 32px;
}
.post-content ol:last-child {
  margin-bottom: 0;
}
.post-content ul {
  list-style: disc;
  padding-left: 32px;
  margin-bottom: 0.5em;
}
.post-content ul:last-child {
  margin-bottom: 0;
}
.post-content table {
  width: 100%;
  font-size: 14px;
  text-align: left;
  color: #64748b;
  border-collapse: collapse;
}
@media (prefers-color-scheme: dark) {
  .post-content table {
    color: #94a3b8;
  }
}
.post-content table td, .post-content table th {
  border: 1px solid #e2e8f0;
}
@media (prefers-color-scheme: dark) {
  .post-content table td, .post-content table th {
    border-color: #cbd5e1;
  }
}
.post-content table td[align=center], .post-content table th[align=center] {
  text-align: center;
}
.post-content table thead {
  color: #334155;
  text-transform: uppercase;
  background-color: #f1f5f9;
}
@media (prefers-color-scheme: dark) {
  .post-content table thead {
    color: #e2e8f0;
    background-color: #64748b;
  }
}
.post-content table thead tr > th {
  padding: 12px 16px;
}
.post-content table tbody tr {
  background-color: #fff;
  border-bottom: 1px solid #f8fafc;
  white-space: nowrap;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody tr {
    border-color: #334155;
    background-color: #1e293b;
  }
}
.post-content table tbody trtr:nth-of-type(2n+1) {
  background-color: #e2e8f0;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody trtr:nth-of-type(2n+1) {
    background-color: #475569;
  }
}
.post-content table tbody tr > th {
  padding: 12px 16px;
  font-weight: 500;
  color: #0f172a;
  white-space: nowrap;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody tr > th {
    color: #fff;
  }
}
.post-content table tbody tr > td {
  padding: 12px 16px;
}
.post-content .x-hide {
  border: 1px solid #ccc;
  padding: 32px 8px 8px;
  border-radius: 4px;
  position: relative;
  background-color: #f3f4f6;
}
.post-content .x-hide br:first-child {
  display: none;
}
.post-content .x-hide.already-hide {
  text-align: center;
}
.post-content .x-hide .x-hide-icon {
  position: absolute;
  display: flex;
  top: 8px;
  right: 8px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  fill: currentColor;
}
.post-content .x-hide .x-hide-icon svg {
  margin-top: -4px;
}
.post-content .x-hide .x-hide-text {
  display: block;
  cursor: pointer;
  user-select: none;
}
.post-content .x-hide:before {
  content: "";
  position: absolute;
  left: 8px;
  top: 10px;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  z-index: 1;
  background: #fc625d;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
}
.post-content .x-hide.blur .x-hide-text {
  filter: blur(5px);
}
.post-content .x-checkbox {
  display: none;
}
.post-content .x-checkbox-label {
  display: inline-flex;
  width: 20px;
  height: 20px;
  margin-bottom: 2px;
  border-radius: 4px;
  position: relative;
  cursor: not-allowed;
  box-sizing: border-box;
  vertical-align: middle;
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: #99f;
  mask-image: url("data:image/svg+xml;base64,");
}
.post-content .x-checkbox:checked + .x-checkbox-label {
  mask-image: url("data:image/svg+xml;base64,");
}